.ico {
  background-image: linear-gradient(163deg, $darkish-blue, $cerulean-three 54%, $darkish-blue);
  padding: 100px 0;
  color: #ffffff;
  overflow: hidden;
  @include r(767) {
    padding: 20px 0; }
  h2 {
    font-size: 45px;
    font-weight: 400;
    margin-bottom: 70px;
    @include r(767) {
      margin-bottom: 20px; } }
  &__block {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 220px;
    position: relative;
    @include r(1024) {
      border: 1px solid #ffffff;
      margin-bottom: 20px; }
    &:before {
      @include r(1200) {
        width: 100%; }
      @include r(1024) {
        content: none; } }
    h3 {
      font-size: 25px;
      font-weight: 500;
      text-transform: uppercase; }
    hr {
      width: 185px; }
    &__percent {
      p {
        font-size: 55px;
        display: inline-block;
        margin-right: 10px;
        font-weight: 600; }
      span {
        font-size: 25px; } }
    span {
      font-family: 'Oswald';
      font-size: 20px;
      font-weight: 300;
      @include r(767) {
        font-size: 18px; } }
    &--1 {
      &:before {
        content: '';
        background: url('../img/ico/1.svg') no-repeat;
        width: 442px;
        height: 222px;
        left: -2px;
        position: absolute; } }
    &--2 {
      &:before {
        content: '';
        background: url('../img/ico/2.svg') no-repeat;
        width: 442px;
        height: 222px;
        left: -8px;
        position: absolute;
        @include r(1200) {
          left: -20px; } } }
    &--3 {
      p {
        font-size: 40px;
        margin-right: 0; }
      &:before {
        content: '';
        background: url('../img/ico/3.svg') no-repeat;
        width: 445px;
        height: 222px;
        left: -31px;
        position: absolute;
        @include r(1200) {
          left: -40px; } } } } }
