.faq {
	@include r(1024) {
		padding: 0 20px; }
	h2 {
		color: $black-three;
		font-size: 45px;
		padding: 70px 0; }
	&__btn {
		font-size: 28px;
		font-weight: 500;
		color: $black-three;
		position: relative;
		padding-bottom: 40px;
		width: 374px;
		transition: all 0.3s;
		min-height: 190px;
		@include r(1024) {
			width: 255px; }
		@include r(767) {
			width: 100%;
			padding-right: 60px; }
		h4 {
			position: relative;
			@include r(767) {
				padding-right: 20px;
				font-size: 23px; }
			&:before {
				content: '';
				position: absolute;
				right: 0px;
				bottom: -10px;
				width: 100%;
				height: 3px;
				background: $yellow-orange; } }
		&.active {
			color: $yellow-orange;
			i {
				transform: rotate(180deg);
				top: 9px; } }
		span {
			width: 65px;
			height: 65px;
			right: 30px;
			border: solid 3px $yellow-orange;
			border-radius: 100px;
			display: inline-block;
			position: relative;
			top: -50px;
			margin: 0 390px;
			color: $yellow-orange;
			@include r(1024) {
				right: 150px; }
			@include r(767) {
				right: 0;
				margin-left: 97%; }
			@include r(500) {
				margin-left: 95%; } }
		i {
			position: absolute;
			right: 17px;
			top: 12px;
			font-size: 40px;
			transition: all 0.3s; } }
	&__hidden {
		display: none;
		p {
			font-size: 20px;
			max-width: 470px;
			color: $black-three; } } }
