.intro {
	padding-top: 270px;
	height: 1080px;
	background-image: linear-gradient(163deg, $darkish-blue, $cerulean-three 54%, $darkish-blue);
	overflow: hidden;
	@include r(1024) {
		height: 100%; }
	@include r(767) {
		padding-top: 200px; }
	.container {
		max-width: 1560px !important;
		height: initial; }
	&__block {
		h2 {
			font-size: 55px;
			font-weight: 500;
			text-transform: uppercase;
			color: #ffffff;
			margin-bottom: 80px;
			@include r(767) {
				margin-bottom: 20px; } }
		ul {
			padding-left: 40px;
			width: 80%;
			@include r(1200) {
        width: 80%; }
			li {
				font-size: 24px;
				margin-bottom: 35px;
				color: #ffffff;
				font-weight: 300;
				position: relative;
				@include r(1200) {
        	font-size: 16px; }
				&:before {
					content: '';
					color: $bright-turquoise;
					border: 1px solid $bright-turquoise;
					border-radius: 50%;
					width: 25px;
					height: 25px;
					left: -37px;
					top: 1px;
					display: flex;
					justify-content: center;
					align-items: center;
					position: absolute; } } } }
	&__social {
		a {
			font-size: 25px;
			text-decoration: none;
			color: #ffffff;
			margin-right: 20px; }
		img {
			position: relative;
			top: 2px; } }
	&__timer {
		position: relative;
		width: 500px;
		height: 500px;
		top: -100px;
		@include r(1023) {
			left: 50%;
			top: 50px;
			transform: translateX(-50%); }
		@include r(767) {
			width: 300px;
			height: 300px; }
		&__circle {
			width: 560px;
			height: 560px;
			border: 1px solid rgba(#ffffff, 0.5);
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			border-radius: 50%;
			&:nth-of-type(2) {
				width: 760px;
				height: 760px; }
			&:nth-of-type(3) {
				width: 1000px;
				height: 1000px; } }
		&__bg {
			color: #ffffff;
			width: 500px;
			height: 500px;
			background-color: $cerulean;
			box-shadow: 0 0 27px 0 rgba(0, 0, 0, 0.17);
			border-radius: 50%;
			display: flex;
			justify-content: center;
			align-items: center;
			flex-direction: column;
			position: relative;
			z-index: 10;
			@include r(767) {
				width: 300px;
				height: 300px; } }
		h3 {
			font-size: 35px;
			font-weight: 500;
			margin-bottom: 15px;
			text-transform: uppercase;
			@include r(767) {
				font-size: 20px; } }
		&__block {
			display: flex;
			p {
				font-size: 52px;
				font-family: 'Oswald';
				font-weight: 200;
				@include r(767) {
					font-size: 30px; } }
			span {
				position: relative;
				top: -10px;
				text-align: center;
				font-size: 20px;
				font-weight: 200;
				font-family: 'Oswald';
				display: block;
				@include r(767) {
					font-size: 15px; } }
			&__dotter {
				display: flex;
				font-size: 48px;
				padding: 0 10px;
				font-weight: 200;
				font-family: 'Oswald';
				@include r(767) {
					font-size: 30px; } } }
		&__btn {
			margin-top: 30px;
			@include r(767) {
				margin-top: 0; }
			a {
				width: 290px;
				height: 50px;
				display: flex;
				border-radius: 22px;
				background-color: $yellow-orange;
				justify-content: center;
				align-items: center;
				text-decoration: none;
				color: #ffffff;
				font-size: 20px;
				transition: all 0.3s;
				cursor: pointer;
				@include r(767) {
					width: 100%;
					height: 40px;
					font-size: 18px; }
				&:hover {
					background: #ffffff;
					color: $yellow-orange; } }
			span {
				margin-top: 10px;
				font-size: 16px;
				text-align: center;
				display: block;
				font-weight: 300;
				@include r(767) {
					font-size: 14px; } } }
		&__user, &__collected {
			position: absolute;
			bottom: 0;
			background-color: $dodger-blue;
			box-shadow: 0 0 27px 0 rgba(0, 0, 0, 0.17);
			border-radius: 50%;
			font-family: 'Oswald';
			display: flex;
			justify-content: center;
			align-items: center;
			flex-direction: column;
			color: #ffffff;
			p {
				font-size: 52px;
				font-weight: 200;
				@include r(767) {
					font-size: 30px; } }
			span {
				font-size: 20px;
				font-weight: 200;
				@include r(767) {
					font-size: 16px; } } }
		&__user {
			width: 240px;
			height: 240px;
			left: -130px;
			@include r(767) {
				width: 120px;
				height: 120px;
				left: 0;
				bottom: -70px; } }
		&__collected {
			width: 180px;
			height: 180px;
			right: 0px;
			bottom: -100px;
			@include r(767) {
				width: 120px;
				height: 120px;
				bottom: -70px; } } }
	&__solutions {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		color: #ffffff;
		margin-bottom: 55px;
		position: relative;
		z-index: 10;
		@include r(1023) {
			margin-top: 170px; }
		h3 {
			font-size: 35px;
			font-weight: 500;
			margin-bottom: 40px; }
		.flex {
			@include r(767) {
				flex-direction: column; } }
		a {
			width: 165px;
			height: 55px;
			background-color: none;
			border: 1px solid #ffffff;
			display: flex;
			justify-content: center;
			align-items: center;
			text-decoration: none;
			color: #ffffff;
			font-size: 25px;
			transition: all 0.3s;
			margin-right: 30px;
			cursor: pointer;
			@include r(767) {
				margin-bottom: 20px;
				margin-right: 0; }
			&:hover {
				background: #ffffff;
				color: $ocean-blue; } } }
	&__bottom__slider {
		height: 120px;
		background: $dodger-blue;
		.container {
			max-width: 1200px !important;
			height: 100%; }
		.swiper-slide {
			display: flex;
			justify-content: center;
			align-items: center;
			img {
				height: 30px; } }
		.swiper-container {
			width: 100%; } }
	svg {
		width: 506px;
		height: 506px;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		@include r(767) {
			width: 306px;
			height: 306px; } }
	canvas {
		position: absolute;
		top: 0;
		z-index: 1; } }
