.team {
	padding-bottom: 80px;
	h2 {
		font-weight: 400;
		font-size: 45px;
		color: $bluish;
		padding: 75px 0; }
	h3 {
		font-size: 28px;
		font-weight: medium;
		color: $black-three;
		padding-top: 10px; }
	span {
		font-size: 21px;
		font-weight: 400;
		color: $bluish;
		display: inline-block;
		padding-bottom: 10px; }
	p {
		font-size: 20px;
		font-weight: 400;
		color: $black-three;
		padding-bottom: 10px; }
	.desc {
		display: none; }
	&__content {
		a {
			width: 40px;
			height: 40px;
			border-radius: 50px;
			display: flex;
			justify-content: center;
			align-items: center;
			background: $bluish;
			text-decoration: none;
			border: 1px solid $bluish;
			margin: 0 auto;
			margin-top: 20px;
			text-decoration: none;
			transition: all 0.3s;
			margin-bottom: 40px;
			svg {
				fill: $white; }
			&:hover {
				background: $white;
				svg {
					fill: $bluish; } } } }
	&__block {
		width: 168px;
		height: 168px;
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 100px;
		box-shadow: 2px 3.5px 7px 0 rgba(0, 0, 0, 0.28);
		margin: 0 auto;
		position: relative;
		border-top: 1px solid #2776b3;
		border-bottom: 1px solid #2776b3;
		box-sizing: border-box;
		background-image: linear-gradient(to right, #023b92, #0189c1 54%, #023b92);
		&--add {
			.team__block__img {
				cursor: default; } }
		&__img {
			background: $white;
			width: 150px;
			height: 150px;
			border-radius: 100px;
			margin: 10px;
			box-shadow: 0 0 0 6px $white-two;
			position: relative;
			overflow: hidden;
			cursor: pointer;
			&:hover .team__block__img__hover {
				opacity: 0.88; }
			&__hover {
				position: absolute;
				left: 0px;
				width: 150px;
				height: 150px;
				opacity: 0;
				background-image: linear-gradient(to right, #023b92, #0189c1 54%, #023b92);
				border-radius: 100px;
				display: flex;
				justify-content: center;
				align-items: center;
				transition: all 0.3s;
				text-decoration: none;
				p {
					padding: 0;
					color: $white;
					font-size: 25px;
					font-weight: 400; } }
			img {
				width: 150px;
				height: 150px;
				border-radius: 100px; } } }
	&__plus {
		width: 40px;
		height: 40px;
		border-radius: 50px;
		position: absolute;
		right: 5px;
		bottom: 5px;
		display: flex;
		justify-content: center;
		align-items: center;
		background: $bluish;
		text-decoration: none;
		border: 1px solid $bluish;
		&:hover {
			background: $white;
			svg {
				fill: $bluish; } }
		svg {
			fill: $white; } } }
