.media {
	background: linear-gradient(to right, #023b92, #0189c1 54%, #023b92);
	position: relative;
	h2 {
		font-size: 45px;
		color: $white;
		padding: 70px 0; }
	#media-slider {
		padding-bottom: 60px;
		// border-bottom: 1px solid #3bacfb
		margin-bottom: 10px; }
	.swiper-slide {
		display: flex;
		flex-direction: column;
		align-items: center; }
	&__block {
		position: relative;
		display: flex;
		flex-direction: column;
		align-items: center;
		img {
			width: 100%; }
		i {
			font-size: 100px;
			position: absolute;
			left: 50%;
			top: 27%;
			transform: translateX(-50%);
			color: #000;
			opacity: 0.88;
			&:hover {
				color: $bluish; } }
		&__shadow {
			height: 5px;
			background: $dodger-blue;
			width: 85%;
			position: relative;
			top: -1px; } }
	p {
		font-size: 20px;
		color: $white;
		padding: 15px 27px 0; }
	.swiper-button-prev, .swiper-button-next {
		background: none;
		width: 120px;
		height: 120px;
		border-radius: 180px;
		border: 2px solid $white-four;
		opacity: 1;
		display: flex;
		justify-content: center;
		align-items: center;
		top: 40%;
		@media(max-width: 1024px) {
			display: none; }
		i {
			font-size: 60px;
			color: $white; }
		&:hover {
			opacity: 0.8; } }
	&__bottom {
		padding-bottom: 140px;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
		@include r(767) {
			padding-bottom: 20px; }
		a {
			display: block;
			margin-right: 30px;
			@include r(767) {
				width: 100%;
				margin-right: 0;
				display: flex;
				justify-content: center;
				margin-bottom: 20px;
				img {
					max-width: 300px;
					max-height: 20px; } } }
		img {} } }
