/* Placeholders */
@media (min-width: 1200px) {
  .timeline__card {
    &:nth-child(odd) .head::after, &:nth-child(even) .head::after {
      position: absolute;
      content: "";
      width: 478px;
      height: 0;
      border-bottom: 1px solid $cerulean-two; }
    &:nth-child(odd) .head::before, &:nth-child(even) .head::before {
      position: absolute;
      content: "";
      width: 9px;
      height: 9px;
      background-color: $cerulean-two;
      border-radius: 9px; } } }



/* Some Cool Stuff */

.timeline__card {
  &:nth-child(1) {
    order: 1; }
  &:nth-child(2) {
    order: 4; }
  &:nth-child(3) {
    order: 2; }
  &:nth-child(4) {
    order: 5; }
  &:nth-child(5) {
    order: 3; }
  &:nth-child(6) {
    order: 6; } }

/* Border Box */

* {
  box-sizing: border-box; }


.timeline {
  padding: 150px 0;
  padding-bottom: 0;
  position: relative;
  @include r(1200) {
    padding: 100px 0 1px; }
  h1 {
    text-align: center;
    font-size: 45px;
    font-weight: 300;
    margin-bottom: 20px;
    position: absolute;
    left: 50%;
    color: #fff;
    transform: translateX(-50%);
    color: $cerulean-two;
    top: -90px; }
  p.leader {
    text-align: center;
    max-width: 90%;
    margin: auto;
    margin-bottom: 45px; }
  &__wrapper {
    position: relative;
    margin: auto;
    &::after {
      z-index: 1;
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      left: 50%;
      border-left: 1px solid rgba(191, 191, 191, 0.4); } }
  &__card {
    position: relative;
    display: block;
    margin: 10px auto 80px;
    max-width: 555px;
    z-index: 2;
    .head {
      position: relative;
      display: flex;
      align-items: center;
      color: #fff;
      font-weight: 400;
      .number-box {
        border-radius: 50%;
        border: 1px solid $cerulean-two;
        float: left;
        width: 130px;
        height: 130px;
        font-size: 35px;
        font-weight: 300;
        display: flex;
        justify-content: center;
        align-items: center;
        color: $cerulean-two;
        @include r(767) {
          font-size: 26px;
          width: 80px;
          height: 80px; } }
      h2 {
        font-size: 28px;
        font-weight: 500;
        line-height: 1.32;
        margin-left: 20px;
        @include r(767) {
          font-size: 20px;
          width: 80%;
          margin-left: 10px; } } }
    .body {
      border-top: 0;
      padding: 15px;
      color: #ffffff;
      p {
        font-size: 20px;
        max-width: 410px;
        line-height: 1.4;
        margin-bottom: 15px;
        @include r(767) {
          font-size: 18px; } }
      a {
        color: $cerulean-two;
        text-decoration: none;
        padding-left: 10px;
        i {
          padding-left: 5px;
          position: relative;
          top: 1px; }
        &:hover {
          text-decoration: underline; } } } } }


@media (min-width: 1200px) {
  .timeline__wrapper {
    display: flex;
    flex-flow: column wrap;
    width: 1170px;
    height: 1650px;
    margin: 0 auto; } }

@media (min-width: 1200px) {
  .timeline__wrapper::after {
    border-left: 1px solid $cerulean-three; } }

@media (min-width: 480px) {
  .timeline__card {
    max-width: 60%; } }

@media (min-width: 720px) {
  .timeline .timeline__card {
    max-width: 60%; } }

@media (min-width: 1200px) {
  .timeline .timeline__card {
    max-width: 550px;
    height: 400px;
    margin: -10px;
    margin-top: 45px;
    margin-bottom: 45px;
    &:nth-child(odd) {
      margin-right: 45px;
      .head {
        &::after {
          left: 116px;
          bottom: 25px; }
        &::before {
          bottom: 21px;
          left: 591.5px; } }
      .body {
        padding-left: 150px; } }
    &:nth-child(even) {
      margin-left: 26px;
      .head {
        display: flex;
        flex-direction: row-reverse;
        &::after {
          right: 116px;
          bottom: 25px; }
        &::before {
          bottom: 21px;
          right: 589.5px; }
        h2 {
          width: 70%;
          position: relative;
          left: -50px; } }
      .body {
        position: relative;
        left: -31px; } }
    &:nth-child(2) {
      margin-top: 180px; } } }

@media (min-width: 480px) {
  .timeline .timeline__card .head h2 {} }

@media (min-width: 480px) {
  .timeline .timeline__card .head h2 span {
    font-size: 0.8rem; } }

@media (min-width: 1200px) {
  .timeline .timeline__card .body {
    height: 315px; } }
