.date {
	padding-top: 100px;
	position: relative;
	overflow: hidden;
	&__pagination {
		padding-bottom: 20px;
		border-bottom: 5px solid $yellow-orange;
		&__block {
			border: solid 2px $bluish;
			margin-bottom: 10px;
			transition: all 0.3s;
			&.active {
				background: $yellow-orange;
				border: solid 2px $yellow-orange;
				h2 {
					color: $white; } }
			h2 {
				font-size: 25px;
				font-weight: 400;
				color: $bluish;
				padding: 20px 0; } } }
	.swiper-slide {
		padding-bottom: 50px;
		padding-right: 10px;
		width: 100%;
		box-sizing: border-box; }






	&__block {
		padding-top: 75px;
		span {
			color: $bluish;
			font-size: 30px;
			padding-top: 15px;
			padding-bottom: 40px;
			display: inline-block;
			padding-left: 85px;
			position: relative;
			@include r(767) {
				font-size: 20px;
				padding-left: 24px; }
			p {
				font-family: Oswald;
				font-size: 80px;
				color: $yellow-orange;
				position: absolute;
				left: -87px;
				top: -30px;
				@include r(767) {
					font-size: 60px;
					left: -40px; } } }
		p {
			color: $black-three;
			padding-bottom: 50px;
			padding-left: 85px;
			font-size: 30px;
			font-weight: 500;
			@include r(767) {
				padding-left: 0; } }
		a {
			padding: 20px 65px;
			display: inline-block;
			background: $bluish;
			border: 2px solid $bluish;
			font-size: 25px;
			color: $very-light-pink;
			text-decoration: none;
			margin-bottom: 20px;
			margin-right: 5px;
			@include r(767) {
				width: 99%;
				padding: 20px 0; } } }
	&__block__bottom {
		border-bottom: 1px solid $white-four;
		h3 {
			font-size: 20px;
			width: 98%;
			height: 40px;
			color: $white;
			background: $yellow-orange;
			display: inline-block;
			text-align: center;
			padding-top: 8px;
			margin-left: 3px;
			margin-right: 5px;
			font-weight: 300; }
		&__presale {
			box-shadow: 2px 3.5px 18px 0 rgba(0, 0, 0, 0.1);
			padding: 20px 35px;
			margin-bottom: 60px;
			margin-left: 5px;
			margin-right: 10px;
			display: flex;
			flex-direction: column;
			&--limited {
				padding: 20px 80px; }
			span {
				font-size: 35px;
				color: $warm-grey-two; }
			p {
				font-size: 17px;
				font-weight: 500;
				color: $warm-grey-two;
				display: inline-block;
				position: relative;
				span {
					position: absolute;
					left: 0;
					bottom: -10px;
					width: 100%;
					background: $white-two;
					height: 1px;
					display: block; } }
			b {
				font-size: 65px;
				color: $yellow-orange;
				font-family: "Oswald";
				font-weight: 400;
				display: block; } } }




	&__benefits {
		&__block {
			border-bottom: solid 1px $pinkish-grey-two;
			padding: 80px 10px 40px;
			span {
				display: block;
				font-family: "Oswald";
				font-size: 80px;
				color: $yellow-orange;
				line-height: 70px;
				padding-bottom: 20px;
				text-transform: uppercase;
				@include r(767) {
					font-size: 70px; } }
			b {
				width: 200px;
				display: inline-block;
				font-size: 17px;
				color: $warm-grey-two;
				padding-bottom: 20px;
				img {
					width: 30px;
					height: 30px;
					position: relative;
					top: 10px;
					left: 10px; } }
			p {
				padding-bottom: 40px;
				font-size: 22px;
				color: $black-three; }
			h3 {
				padding-bottom: 40px;
				font-size: 30px;
				font-weight: 500;
				color: $black-three; } } }





	&__bonus {
		&__top {
			padding: 80px 10px 0;
			b {
				font-size: 25px;
				font-weight: 500;
				color: $black-three;
				padding-bottom: 40px;
				display: inline-block; }
			h3 {
				font-size: 45px;
				font-weight: 400;
				color: $bluish;
				padding-bottom: 60px;
				@include r(767) {
					font-size: 35px; } }
			p {
				width: 89%;
				margin: 0 auto;
				font-size: 24px;
				color: $black-three;
				@include r(1024) {
					width: 100%; } } }
		&__center {
			padding: 20px 20px;
			&__block {
				border-bottom: solid 1px $pinkish-grey-two;
				padding-top: 80px;
				b {
					font-size: 30px;
					font-weight: 500;
					color: $black-three;
					position: relative;
					padding-left: 50px;
					display: inline-block;
					@include r(767) {
						padding-bottom: 40px; }

					span {
						font-family: "Oswald";
						font-size: 80px;
						color: $yellow-orange;
						line-height: 70px;
						position: absolute;
						top: -25px;
						left: -5px;
						font-weight: 400; } }
				p {
					font-size: 22px;
					color: $black-three;
					padding-bottom: 40px; } } }
		&__bottom {
			padding-top: 100px;
			h3 {
				font-size: 45px;
				color: $bluish;
				max-width: 80%;
				margin: 0 auto;
				padding-bottom: 80px;
				@include r(1024) {
					width: 100%;
					font-size: 35px; } }
			p {
				font-size: 22px;
				color: $black-three;
				position: relative;
				padding-left: 40px;
				padding-bottom: 30px;
				i {
					position: absolute;
					left: 0;
					top: 0;
					color: $bluish; } } } }




	.swiper-button-prev, .swiper-button-next {
		height: 100%;
		width: 120px;
		background: none;
		opacity: 1;
		top: 0;
		div {
			background: #2776b3;
			width: 120px;
			height: 120px;
			border-radius: 180px;
			opacity: 1;
			display: flex;
			justify-content: center;
			align-items: center;
			top: 320px;
			position: absolute;
			@include r(1024) {
				display: none; }
			img {
				width: 20px;
				height: 34px; }
			&:hover {
				opacity: 1; } } }
	.swiper-button-prev {
		div {
			img {
				transform: rotate(180deg); } } } }
