.header {
	position: absolute;
	top: 0;
	width: 100%;
	padding-top: 50px;
	z-index: 20;
	transition: all 0.3s;
	&.fixed {
		@include rmin(768) {
			position: fixed;
			background-image: linear-gradient(163deg, $darkish-blue, $cerulean-three 54%, $darkish-blue);
			padding: 20px 0;
			transition: all 0.3s; } }
	.container {
		max-width: 1560px !important; }
	&__logo {
		width: 195px;
		height: 44px;
		display: inline-block;
		@include r(767) {
			width: 160px;
			margin-top: 20px;
			img {
				width: 100%; } } }
	&__lang {
		display: flex;
		margin-left: 45px;
		@include r(767) {
			width: 86px;
			margin-top: 20px;
			margin-left: 20px; }
		span {
			border-left: 1px solid #fff;
			display: block;
			height: 45px;
			margin-right: 20px; }
		a {
			display: block;
			text-decoration: none;
			color: #fff;
			position: relative;
			&:before {
				content: '\f107';
				position: absolute;
				right: -30px;
				top: 50%;
				transform: translateY(-50%);
				font: normal normal normal 28px/1 FontAwesome; } } }
	.lang__img {
		width: 34px;
		height: 34px;
		border: 2px solid #ffffff;
		border-radius: 50%;
		&:hover {} }
	nav {
		float: right;
		@include r(1023) {
			margin-top: 30px;
			float: none; }
		@include r(767) {
			display: none; }
		ul {
			display: flex;
			@include r(1023) {
				justify-content: space-between; }
			li {
				margin-right: 20px; }
			a {
				text-decoration: none;
				color: #fff;
				font-size: 22px;
				font-weight: 300;
				transition: all 0.3s;
				@include r(1200) {
					font-size: 16px; }
				@include r(1023) {
					font-size: 22px; }
				&:hover {
					border-bottom: 1px solid #ffffff; } } } }
	&__whitepaper {
		border: 2px solid #ffffff;
		border-radius: 17px;
		width: 155px;
		height: 40px;
		display: flex;
		justify-content: center;
		align-items: center;
		cursor: pointer;
		margin-right: 10px;
		transition: all 0.3s;
		position: relative;
		z-index: 20;
		@include rmin(768) {
			margin-right: 155px; }
		@include r(767) {
			margin-top: 20px; }
		span {
			color: #ffffff;
			font-size: 22px;
			position: relative;
			left: -12px;
			@include r(1200) {
				font-size: 16px; }
			&:before {
				content: '\f107';
				position: absolute;
				right: -25px;
				top: 50%;
				transform: translateY(-50%);
				font: normal normal normal 28px/1 FontAwesome; } }
		ul {
			background: #ffffff;
			position: absolute;
			box-shadow: 2px 3.464px 17.64px 0.36px rgba(0, 0, 0, 0.28);
			top: 55px;
			width: 155px;
			text-align: left;
			box-sizing: border-box;
			font-size: 16px;
			display: none;
			li {
				line-height: 1.8;
				padding: 0 15px;
				text-align: center;
				transition: all 0.3s;
				&:hover {
					color: #ffffff;
					background: $ocean-blue; }
				&:not(:last-child) {
					border-bottom: 1px solid #eeeeee; } } }
		&:hover {
			background: #ffffff;
			span {
				color: $ocean-blue; } } }
	&__sing {
		width: 130px;
		border-radius: 17px;
		height: 40px;
		background: #ffffff;
		text-decoration: none;
		border: 2px solid #ffffff;
		display: flex;
		justify-content: center;
		align-items: center;
		cursor: pointer;
		transition: all 0.3s;
		@include rmin(768) {
			position: fixed; }
		@include r(767) {
			margin-top: 20px; }
		span {
			color: $ocean-blue;
			font-size: 20px;
			left: 15px;
			position: relative;
			@include r(1024) {
				font-size: 16px; }
			&:before {
				content: '\f007';
				position: absolute;
				left: -30px;
				top: 50%;
				transform: translateY(-50%);
				font: normal normal normal 28px/1 FontAwesome; } }
		&:hover {
			background: none;
			span {
				color: #ffffff; } } } }


.open-menu {
	position: absolute;
	top: 15px;
	left: 50%;
	transform: translateX(-50%);
	i {
		color: #ffffff;
		font-size: 40px; }
	@include rmin(767) {
		display: none; } }

.close-menu {
	position: absolute;
	top: 45px;
	left: 50%;
	transform: translateX(-50%);
	i {
		color: #ffffff;
		font-size: 40px; } }

.mobile-menu {
	display: none;
	background-image: linear-gradient(163deg, $darkish-blue, $cerulean-three 54%, $darkish-blue);
	position: fixed;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: 100;
	nav {
		height: 100%; }
	ul {
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		a {
			text-decoration: none;
			color: #fff;
			font-size: 28px;
			font-weight: 300;
			transition: all 0.3s;
			margin-bottom: 20px;
			display: inline-block; } } }

.lang__body {
	background: rgba(0, 0, 0, 0.8);
	opacity: 0;
	position: absolute;
	z-index: 99;
	width: 100%;
	height: 100%;
	visibility: hidden;
	&.active {
		opacity: 1;
		visibility: visible; } }
.lang__block {
	background: #ffffff;
	padding: 50px;
	margin: 0 5px;
	position: absolute;
	border-radius: 35px;
	z-index: 100;
	top: 110px;
	display: none;
	&__popular {
		margin-bottom: 75px; }
	h2 {
		font-size: 25px;
		font-weight: 500;
		color: $black-three;
		text-transform: uppercase;
		font-weight: 500;
		margin-bottom: 30px; }
	a {
		display: inline-block;
		margin-right: 15px;
		margin-bottom: 15px;
		width: 35px;
		height: 35px;
		img {
			width: 35px;
			height: 35px;
			border: 1px solid #ffffff;
			padding: 1px;
			border-radius: 50%;
			transition: all 0.3s; }
		&:hover img {
			border: 1px solid #00e2e2;
			padding: 1px;
			border-radius: 50%; } } }
