.footer {
	background: $dark-grey;
	padding: 20px 0 0px;
	&__top {
		border-bottom: 2px solid $darkish-blue;
		padding-bottom: 30px;
		display: flex;
		justify-content: center;
		ul {
			display: flex;
			@include r(640) {
				flex-direction: column; } }
		a {
			padding: 12px;
			text-decoration: none;
			display: inline-block;
			color: $white;
			font-size: 20px;
			font-weight: 00;
			&:hover {
				color: $darkish-blue; } } }
	&__bottom {
		padding: 10px 0;
		border-bottom: 2px solid $darkish-blue;
		a {
			font-size: 25px;
			padding: 30px;
			color: $white;
			text-decoration: none;
			@include r(767) {
				padding: 20px; }
			&:hover {
				color: $darkish-blue; } }
		&--2 {
			padding: 20px 0;
			border-bottom: 2px solid $darkish-blue;
			@include r(640) {
				display: flex;
				flex-direction: column; }
			a {
				color: #ffffff;
				margin-right: 20px;
				text-decoration: none; } } }
	&__copyright {
		padding: 20px 0;
		a {
			color: #ffffff;
			margin-right: 20px;
			text-decoration: none; } } }

