.global {
	padding-top: 60px;
	padding-bottom: 60px;
	&__heading {
		text-align: center;
		span {
			font-size: 25px;
			font-weight: 500;
			color: $black-three;
			text-transform: uppercase;
			margin-bottom: 40px;
			display: inline-block;
			@include r(767) {
				font-size: 20px; } }
		h2 {
			font-size: 45px;
			line-height: 1.22;
			color: $bluish;
			font-weight: 300;
			margin-bottom: 70px;
			@include r(767) {
				font-size: 30px;
				margin-bottom: 20px; } }
		p {
			font-size: 24px;
			line-height: 1.29;
			color: $black-three;
			font-weight: 300;
			margin-bottom: 90px;
			@include r(767) {
				font-size: 20px;
				margin-bottom: 20px; } } }
	&__block {
		border-width: 2px;
		border-style: solid;
		border-image: linear-gradient(163deg, $darkish-blue, $cerulean-three 54%, $darkish-blue) 1% 1%;
		height: 300px;
		position: relative;
		padding: 20px;
		box-sizing: border-box;
		&.active {
			box-shadow: inset 0px 0px 10px 15px rgba($cerulean-three, 0.25); }
		&--margin {
			margin-bottom: 30px; }
		@include r(1150) {
			width: 100%;
			height: 100%; }
		&__heading {
			display: flex;
			margin-bottom: 20px;
			align-items: flex-end;
			img {
				width: 70px;
				height: 70px; }
			h2 {
				font-size: 22px;
				font-weight: 500;
				line-height: 1.25;
				color: $black-three;
				margin-left: 20px;
				position: relative;
				top: 10px;
				@include r(767) {
					font-size: 22px; } } }
		ul {
			li {
				font-size: 16px;
				line-height: 1.41;
				color: $black-three;
				margin-bottom: 10px; } }
		a {
			position: absolute;
			bottom: -20px;
			background: $bluish;
			width: 80%;
			height: 40px;
			color: #ffffff;
			display: flex;
			justify-content: center;
			align-items: center;
			text-decoration: none;
			font-size: 16px;
			font-weight: 300;
			left: 50%;
			transform: translateX(-50%);
			border: 2px solid $bluish;
			transition: all 0.3s;
			@include r(767) {
      	width: 90%; }
			&:hover {
				background: #ffffff;
				color: $bluish; } } } }

